import { GET_USERS } from "./types";

const initialState = {
  data: [],
};

export const panelTopReducer = (state = initialState, action) => {
  console.log({
    "panelTopReducer state:": { ...state, data: action.payload },
    "panelTopReducer action": action.payload,
  });
  switch (action.type) {
    case GET_USERS:
      return { ...state, data: action.payload };
      break;

    default:
      return state;
  }
};
